<template>
    <div id="app">
        <button @click="scan">扫一扫</button>
        <div>result: {{ result }}</div>
    </div>
</template>

<script>
import wx from 'weixin-js-sdk';
export default {
    name: 'App',
    components: {},
    data() {
        return {
            result: '',
        };
    },
    created() {
        wx.config({
            debug: true, // 开启调试模式,开发时可以开启
            appId: 'wxd8c2f7f1955ab6ba', // 必填，公众号的唯一标识   由接口返回
            timestamp: 1634865805, // 必填，生成签名的时间戳 由接口返回
            nonceStr: '0a9f3543034144578a14349de1451741', // 必填，生成签名的随机串 由接口返回
            signature: '99e1b9670d14403cc801ab4f107f14eb7210e79e', // 必填，签名 由接口返回
            jsApiList: ['checkJsApi', 'scanQRCode'], // 此处填你所用到的方法
        });
        wx.error(function (res) {
            alert('出错了：' + res.errMsg); //这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
    },
    methods: {
        scan() {
            wx.scanQRCode({
                needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                scanType: ['qrCode'], // 可以指定扫二维码还是一维码，默认二者都有
                success: function (res) {
                    // self.$toast('weixin scanQRCode success', 'success');
                    this.result = res.resultStr;
                },
            });
        },
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
</style>
